<template>
  <div class="login">
    <van-nav-bar @click-right="goindex">
      <template #left>
        <div class="login-title">Meeting Coffee</div>
      </template>
      <template #right>Home</template>
    </van-nav-bar>
    <div class="login-welcome">
      <h3>
        <img src="../assets/images/logo.png" class="auto-img" alt />
      </h3>
      <p>Meeting is Fate</p>
    </div>
    <div class="login-form">
      <van-form>
        <van-field label="手机号" placeholder="请输入手机号" v-model="loginInfo.phone" />
        <van-field
          :type="isPassword ? 'password':'text'"
          label="密码"
          placeholder="请输入密码"
          :right-icon="isPassword ? 'closed-eye':'eye-o'"
          v-model="loginInfo.password"
          @click-right-icon="showPassword"
        />
        <div class="forget clearfix">
          <span class="fr" @click="goForget">忘记密码?</span>
        </div>
        <div class="login-btn">
          <van-button round block type="info" native-type="submit" @click="login">登录</van-button>
        </div>
        <div class="regist-btn">
          <div class="regist-contents">
            没有账号?
            <span @click="showPopup">立即注册</span>
          </div>
          <van-popup v-model="show" round position="bottom" :style="{ height: '50%' }" closeable>
            <div class="regist-content">
              <div class="regist-title">
                <h1>注册</h1>
              </div>
              <div class="regist-mes">
                <van-form>
                  <van-field label="昵称" placeholder="请输入1-4位昵称" v-model="registInfo.nickName" />
                  <van-field label="手机号" placeholder="请输入11位手机号" v-model="registInfo.phone" />
                  <van-field
                    :type="isPassword ? 'password':'text'"
                    label="密码"
                    placeholder="请输入6-16位且为字母开头的密码"
                    :right-icon="isPassword ? 'closed-eye':'eye-o'"
                    @click-right-icon="showPassword"
                    v-model="registInfo.password"
                  />

                  <div class="regist-btn2">
                    <van-button round block type="info" native-type="submit" @click="regist">注册</van-button>
                  </div>
                </van-form>
              </div>
            </div>
          </van-popup>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import "../assets/less/login.less";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      loginInfo: {
        phone: "",
        password: "",
      },
      registInfo: {
        phone: "",
        password: "",
        nickName: "",
      },

      isPassword: true,
      show: false,
    };
  },
  methods: {
    goindex() {
      this.$router.push({ name: "Index" });
    },
    goForget() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({ name: "Forgetpassword" });
      }, 1000);
    },
    showPassword() {
      this.isPassword = !this.isPassword;
    },
    showPopup() {
      this.show = true;
    },
    //注册
    regist() {
      //获取到用户输入的注册信息
      let userInfo = this.registInfo;
      //验证表单
      let registw = {
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,4}$/,
          errorMsg: "昵称格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
      };
      if (!validForm.valid(registw)) {
        return;
      }

      //发起请求，加载中
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0,
      });

      //序列化参数,复制对象
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);

      this.axios({
        method: "post",
        url: "/register",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 102) {
            this.$notify({
              message: result.data.msg,
              color: "#fff",
              background: "#3585f9",
            });
          }
          if (result.data.code == 100) {
            this.$notify({
              message: result.data.msg,
              color: "#fff",
              background: "#3585f9",
            });
            this.show = false;
            for (let key in userInfo) {
              userInfo[key] = ""; //成功之后清空注册表单里的数据
            }
          } else {
            this.$notify({
              message: result.data.msg,
              color: "#fff",
              background: "#3585f9",
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //登录
    login() {
      let userInfo = this.loginInfo;

      let loginw = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };
      if (!validForm.valid(loginw)) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0,
      });
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);
      this.axios({
        method: "post",
        url: "/login",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 200) {
            localStorage.setItem("WTPTK", result.data.token); //保存用户存储到本地
            this.$notify({
              message: result.data.msg,
              color: "#fff",
              background: "#3585f9",
              duration: 800,
            });
            this.$router.push({ name: "Index" });
          } else {
            this.$notify({
              message: result.data.msg,
              color: "#fff",
              background: "#3585f9",
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

